import React, { Component } from 'react'
import { Button, Image, Table, Form, OverlayTrigger, Tooltip, Dropdown } from 'react-bootstrap';
import Author from '../../../CommonComponents/Author/Author';
import { Link } from 'react-router-dom';
import SelectAccount from './SelectAccount'
import HTMLReactParser from 'html-react-parser';
import { connect } from 'react-redux'
import { GET_FEEDS } from '../../../../../../actions/manageFeedsAction'
import { GET_NETWORK_NAME, showAddAccounntAlert, switchWarning } from '../../../../../../utils'
import { GET_NETWORK_ICON } from '../../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { checkPermission } from '../../../../../../actions/collaboratorActions';

class FeedsList extends Component {
    constructor(props) {
        super(props)

        this.state = {
            moderation: this.props.feed.moderation,
            status: this.props.feed.status,
            connectedAccount: this.getConnectAccount(),
        }
    }

    getConnectAccount = () => {
        const { accountList, connectedAccount } = this.props.feed;
        const connectedAcc = accountList.length > 0 ? accountList.filter((account) => account.id === connectedAccount) : [];
        return connectedAcc.length > 0 ? connectedAcc[0] : accountList.length > 0 ? accountList[0] : {}
    }

    componentDidMount() {
        const { feed } = this.props
        this.setState({ status: feed.status, moderation: feed.moderation })
    }

    onModerationSwitch = (status) => {
        const { feed, changeStatus } = this.props
        changeStatus(feed.id, Number(status), 'moderation')
        this.setState({ moderation: Number(status) })
    }

    onFeedStatusSwitch = (status) => {
        const { feed, changeStatus } = this.props
        changeStatus(feed.id, Number(status), 'status')
        this.setState({ status: Number(status) })
    }

    connectAccount = (obj, status) => {
        const item = {
            id: obj.networkId,
            name: (obj.networkName).toLowerCase()
        }
        localStorage.setItem("feedId", obj.id);
        if (status) localStorage.setItem("refresh", true);
        localStorage.setItem("connectionType", 'manageFeed');
        showAddAccounntAlert(item, status, true)
    }

    componentDidUpdate(prevProps) {
        if (prevProps.accountChanged != this.props.accountChanged) {
            this.setState({ connectedAccount: this.getConnectAccount() })
        }
    }

    render() {
        const { feed, onFeedDelete, changeAccount } = this.props
        const { status, moderation, connectedAccount } = this.state
        const { filterId, networkId, accountList, isBtntext } = feed
        return (
            <tr>
                <td>
                    <div className="d-flex align-items-center">
                        <span className="h-34px w-34px me-2 d-flex flex-shrink-0">
                            <Image className="h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(feed.networkId)}.svg`)} height={43} width={43} title={GET_NETWORK_NAME(feed.networkId)} alt={GET_NETWORK_NAME(feed.networkId)} fluid/>
                        </span>
                        <div style={{ minWidth: 170 }}>
                            <p className="mb-0 d-flex">
                                <span className="feed_name fw-semibold">{HTMLReactParser(feed.name)}{" "}</span>
                                {feed.socialLink ?
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{feed.tooltip}</Tooltip>}>
                                        <Link className="ms-2 text-muted" to={feed.networkId !== 8 ? feed.socialLink : `https://vimeo.com/user${feed.accountId}`} target="_blank" title='support'>
                                            <FontAwesomeIcon icon={'arrow-up-right-from-square'} />
                                        </Link>
                                    </OverlayTrigger>
                                    : null}
                            </p>
                            <small className="mb-0 text-gray-700">{feed.networkName}</small>
                        </div>
                    </div>
                </td>
                <td>
                    <div className="d-flex align-items-center">
                        {[1, 2, 3, 4, 5, 8, 65, 67, 26, 23, 86].includes(filterId) && networkId != 2 && accountList && accountList.length > 0 ?
                            <Dropdown style={{ minWidth: 220 }}>
                                <Dropdown.Toggle variant="secondary" size={'sm'} className="d-flex align-items-center w-100 justify-content-between">
                                    <div className="d-flex align-items-center flex-grow-1">
                                        <Author className={'symbol symbol-26 symbol-circle flex-shrink-0 me-2'}
                                            imageSize={26}
                                            symbolClass={'symbol-label fs-8'}
                                            name={connectedAccount.accountName} profileUrl={connectedAccount.profilePicture} networkId={networkId} />
                                        <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                                            <p className="fw-medium mb-0 text-ellipsis fs-9">{connectedAccount.accountName}</p>
                                        </div>
                                    </div>
                                    {(isBtntext == "Reconnect" || isBtntext == "Connect") ?
                                        <OverlayTrigger overlay={<Tooltip>Feed disconnected as the social media account's user name got changed since your last connection with Tagbox. Kindly click on the button below to connect.</Tooltip>}>
                                            <span className='me-1 fs-5 text-danger h-14px w-14px'>
                                                <FontAwesomeIcon icon={'circle-info'} />
                                            </span>
                                        </OverlayTrigger>
                                        : null}
                                </Dropdown.Toggle>

                                <Dropdown.Menu className="w-100">
                                    {accountList.map(account => {
                                        const { id, profilePicture, accountName } = account;
                                        return <Dropdown.Item key={id} onClick={() => checkPermission('networkManager', 'manageFeed', true) ? changeAccount(id, feed.id) : {}}>
                                            <div className="d-flex align-items-center">
                                                <Author className={'symbol symbol-26 symbol-circle flex-shrink-0 me-2'}
                                                    imageSize={26}
                                                    symbolClass={'symbol-label fs-8'}
                                                    name={accountName} profileUrl={profilePicture} networkId={networkId} />
                                                <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                                                    <p className="fw-medium mb-0 text-ellipsis fs-9">{accountName}</p>
                                                </div>
                                            </div>
                                        </Dropdown.Item>
                                    })}
                                </Dropdown.Menu>
                            </Dropdown>
                            : '--'}
                        <div className="d-flex ms-3">
                            {isBtntext == "Reconnect" ?
                                <OverlayTrigger placement="top" overlay={<Tooltip>Reconnect</Tooltip>}>
                                    <Button variant='icon' size='sm' className='btn-active-primary' aria-label='Reconnect' onClick={() => checkPermission('networkManager', 'manageAccount', true) ? this.connectAccount(feed, true) : {}}>
                                        <FontAwesomeIcon type={'solid'} icon={'rotate'} />
                                    </Button>
                                </OverlayTrigger>
                                : ''}
                            {isBtntext == "Connect" ?
                                <OverlayTrigger placement="top" overlay={<Tooltip>Connect</Tooltip>}>
                                    <Button variant='icon' size='sm' className="btn-active-primary" aria-label='Connect' onClick={() => checkPermission('networkManager', 'manageAccount', true) ? this.connectAccount(feed, false) : {}}>
                                        <FontAwesomeIcon type={'solid'} icon={'rotate'} />
                                    </Button>
                                </OverlayTrigger>
                                : ''}
                        </div>
                    </div>
                </td>
                {/* <td>
                    <div className="d-flex align-items-center">

                            {feed && feed.accountList && feed.accountList.length ?
                            feed.filterId && [1, 2, 3, 4, 5, 8, 65, 67, 26].includes(feed.filterId) && feed.networkId != 2 ?
                                feed.accountList.filter(x => x.id == feed.connectedAccount) ? <>

                                    <SelectAccount changeAccount={changeAccount} feed={feed} key={feed.id} />
                                </>
                                    : ''
                                : ''
                            : ''}
                        
                        <div className="d-flex ms-3">
                            {feed.isBtntext == "Reconnect" ?
                                <OverlayTrigger placement="top" overlay={<Tooltip>Reconnect</Tooltip>}>
                                    <button type="button" className="btn btn-icon btn-active-primary p-0 w-32px h-32px" onClick={() => this.connectAccount(feed, true)}>
                                        <FontAwesomeIcon icon={'arrow-rotate'} />
                                    </button>
                                </OverlayTrigger>
                                : ''}
                            {feed.isBtntext == "Connect" ?
                                <OverlayTrigger placement="top" overlay={<Tooltip>Connect</Tooltip>}>
                                    <button type="button" className="btn btn-icon btn-active-primary p-0 w-32px h-32px" onClick={() => this.connectAccount(feed, false)}>
                                        <FontAwesomeIcon icon={'arrow-rotate'} />
                                    </button>
                                </OverlayTrigger>
                                : ''}
                        </div>
                    </div>
                </td> */}
                <td>
                    <p className="fs-8 fw-semibold mb-0" data-id="post" style={{minWidth: 120}}>{feed.postCount ? feed.postCount : 0}</p>
                    <span className="fs-8 text-gray-700">Approved {feed.postCount > 1 ? 'posts' : 'post'}</span>
                </td>
                <td>
                    <Form.Check type="switch" checked={moderation} data-id="moderation" onChange={() => checkPermission('networkManager', 'manageFeed', true) ? moderation ? this.onModerationSwitch(!moderation) : switchWarning('Enabling moderation will make all newly received posts private until they are reviewed and approved.',this.onModerationSwitch,!moderation) : {}} />
                </td>
                <td>
                    <Form.Check type="switch" checked={status} data-id="status" onChange={() => checkPermission('networkManager', 'manageFeed', true) ? !status ? this.onFeedStatusSwitch(!status) : switchWarning('Disabling this status will hide all collected posts from this feed, affecting visibility and content access.',this.onFeedStatusSwitch,!status) : {}} />
                </td>
                <td className="pr-0 text-end">
                    <div className="d-flex align-items-center justify-content-end" onClick={() => checkPermission('networkManager', 'manageFeed', true) ? onFeedDelete(feed.id, feed.networkId) : {}}>
                        <OverlayTrigger placement="top" overlay={<Tooltip>Delete</Tooltip>}>
                            <Button variant='icon' size='sm' data-id="delete" className="btn-active-primary" aria-label="delete">
                                <FontAwesomeIcon type="solid" icon={'trash-can'} />
                            </Button>
                        </OverlayTrigger>
                    </div>
                </td>
            </tr>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        GET_FEEDS: () => dispatch(GET_FEEDS()),
    }
}

export default connect(null, mapDispatchToProps)(FeedsList);

