import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Container, Table, Card, Button } from 'react-bootstrap';
import { ACTION_DELETE_FEED, ACTION_FEED_STATUS, ACTION_UPDATE_SOCIAL_ACCOUNT, GET_FEEDS } from '../../../../../actions/manageFeedsAction';
import FeedsList from './Components/FeedsList';
import { Loader } from '../../CommonComponents/Loader';
import Swal from 'sweetalert2'
import { addAccountFromId } from '../../../../../utils/addAccount';
import { getModeration } from '../../../../../actions/moderationActions';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import withRouter from '../../../../routes/WithRouter';
import EmptyWithIcon from '../../CommonComponents/Empty/EmptyWithIcon';
import { TOGGLE_ADD_GALLERY } from '../../../../../reducer/keys';
import { SNAPUP_DETAILS_ACTION } from '../../../../../actions/addFeedActions';
import { checkPermission } from '../../../../../actions/collaboratorActions';
import SubHeaderCrumbs from '../../CommonComponents/SubHeader';
import { parseAUTHJson } from '../../../../../utils';

const tableHeaders = [
    { name: "Name", minWidth: 150 },
    { name: "Account", minWidth: 150 },
    { name: "Posts", minWidth: 120 },
    { name: "Moderation", minWidth: 50 },
    { name: "Status", minWidth: 50 },
    { name: "Actions", minWidth: 70, align: 'text-end' }
];
class Managefeeds extends Component {
    state = { currentPage: 1, showPost: 30, totalPage: 1, allFeeds: [], currentPageFeeds: null, accountChanged: false }

    componentDidMount() {
        window.addEventListener("message", this.handleMessage);
        this.props.GET_FEEDS();
        this.countAllPage()
    }

    componentDidUpdate(prevProps, prevState) {
        if (!this.props.manageFeeds?.loading && !this.props.wallsLoader && !this.props.manageFeeds?.feeds?.length) {
            const wallId = localStorage.getItem('wallId');
            if (wallId) {
                this.props.navigate(`/content/addfeed/${wallId}`);
            }
        }
        else if (this.props.manageFeeds?.feeds?.length == 1) {
            const { id, networkId } = this.props.manageFeeds?.feeds[0]
            if (id == 0 && networkId == 0) {
                const wallId = localStorage.getItem('wallId');
                if (wallId) this.props.navigate(`/content/addfeed/${wallId}`);
            }
        }
    }

    changeTotalPost = (e) => { this.setState({ showPost: parseInt(e.target.value), currentPage: 1 }, () => this.countAllPage()) }

    nextPrev = (no) => { this.setState({ currentPage: no }, () => this.countAllPage()) }

    countAllPage = () => {
        const { manageFeeds } = this.props;
        const feeds = manageFeeds?.feeds
        this.setState({ allFeeds: feeds, totalPage: Math.ceil((feeds && feeds.length > 0 ? feeds.length : 0) / this.state.showPost) });
    }


    changeStatus = (feedId, status, action) => {
        this.props.ACTION_FEED_STATUS(feedId, status, action, this.onSuccessUpdateRedux)
    }


    changeAccount = (id, feedId) => {
        this.props.ACTION_UPDATE_SOCIAL_ACCOUNT(id, feedId, this.onSuccessUpdateRedux)
    }
    onSuccessUpdateRedux = (val) => {
        this.props.getModeration();
        this.countAllPage()
        if (val == 'account') this.setState({ accountChanged: !this.state.accountChanged })
    }

    onFeedDelete = (feedId, networkId) => {
        Swal.fire({
            icon: 'warning',
            title: 'Are you sure?',
            showCloseButton: true,
            showCancelButton: true,
            focusConfirm: true,
            confirmButtonText: 'Yes',
            confirmButtonAriaLabel: 'Yes',
            cancelButtonText: 'No',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
                this.props.ACTION_DELETE_FEED(feedId, networkId, this.onDeleteUpdateRedux(networkId))
            }
        });
    }

    onDeleteUpdateRedux = (networkId) => {
        this.props.getModeration();
        this.countAllPage()
    }

    handleMessage = async (event) => {
        try {
            const response = parseAUTHJson(event.data);
            const { code, networkId, type } = response;
            if (type == "manageFeed") {
                if (code) await addAccountFromId(parseInt(networkId), code, this.props.GET_FEEDS);
            }
        } catch (error) {
            console.log("--- handleMessage error ---", error);
        }
    };

    componentWillUnmount() {
        window.removeEventListener("message", this.handleMessage);
    }

    getPagination = () => {
        const { currentPage, totalPage } = this.state;
        const pagination = [];

        if (totalPage <= 5) {
            for (let i = 1; i <= totalPage; i++) {
                pagination.push(i);
            }
        } else {
            if (currentPage <= 3) {
                pagination.push(1, 2, 3, 4, '...', totalPage);
            } else if (currentPage > totalPage - 3) {
                pagination.push(1, '...', totalPage - 3, totalPage - 2, totalPage - 1, totalPage);
            } else {
                pagination.push(1, '...', currentPage - 1, currentPage, currentPage + 1, '...', totalPage);
            }
        }

        return pagination;
    }



    render() {
        const { currentPage, showPost, totalPage, allFeeds, accountChanged } = this.state;
        const { manageFeeds, wallsLoader, allWalls, navigate } = this.props;
        const pagination = this.getPagination();

        const startIdx = (currentPage - 1) * showPost;
        const currentFeeds = allFeeds > 0 ? allFeeds.slice(startIdx, startIdx + showPost) : [];
        const getWallData = allWalls?.find((e) => e.id == localStorage.getItem('wallId')) ?? {};

        return <>
            {allWalls?.length ? <SubHeaderCrumbs key={getWallData?.id} crumbs={[{ name: getWallData?.name ?? "", icon: "grid-2-plus",  imgUrl : getWallData?.thumbnail ?? "" },{ name: "Home", link: "/home"},{ name: "Content", link: "/content"}, {name: getWallData?.name, link:`/content/moderation/${getWallData?.id}`}, { name: "Manage feeds", link: `/content/managefeed/${getWallData?.id || localStorage.getItem('wallId') || ''}`}]} RightComponent={<div>
                {!manageFeeds.loading && !wallsLoader ?
                allWalls && allWalls.length > 0 ?
                <button onClick={() => checkPermission('networkManager', 'manageFeed', true) ? navigate(`/content/addfeed/${localStorage.getItem('wallId')}`) : {}} className="btn btn-primary btn-sm text-nowrap btn-min-w">
                    <FontAwesomeIcon icon={'plus'} iconClass="me-1" />
                    Add feed
                </button>
                :
                <Button size='sm' className='text-nowrap btn-min-w' onClick={() => checkPermission('contentGallery', 'createGallery', true) ? this.props.TOGGLE_ADD_GALLERY(true) : {}}>
                    <FontAwesomeIcon icon={'plus'} iconClass="me-1" />
                    Add Gallery</Button> : null}
            </div>} showGallerySwitcher={false} /> : null}
            <div className='d-flex flex-column flex-column-fluid full-content mt-10 mt-lg-0'>
                <Container>
                    <Card>
                        {/* <Card.Header className='f-between d-flex'>
                            <div className="f-center">
                            
                            </div>
                        </Card.Header> */}
                        <Card.Body>
                            <div className="mb-xl-5 mb-4 d-flex flex-column justify-content-between p-3 position-relative" style={{ minHeight: 'calc(100vh - 210px)' }}>
                                {!manageFeeds.loading && !wallsLoader ?
                                    allWalls && allWalls.length > 0 ?
                                        <div className='table-responsive' style={{ minHeight: 'calc(100vh - 235px)' }}>
                                            <Table size='lg' className='pb-10'>
                                                <thead className="thead-light">
                                                    <tr>
                                                        {
                                                            tableHeaders.map((header, i) => <th key={i} className={`fw-bold text-nowrap ${header?.align || ''}`} style={{ minWidth: header.minWidth }}>{header.name}</th>)
                                                        }
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {manageFeeds?.feeds && manageFeeds?.feeds.length ? manageFeeds?.feeds.map((feed) => {
                                                        return !(feed?.id == 0 && feed?.networkId == 0) ? <FeedsList feed={feed} changeStatus={this.changeStatus} onFeedDelete={this.onFeedDelete} changeAccount={this.changeAccount} key={`tr_${feed.id}`} accountChanged={accountChanged} /> : null
                                                    }) : null}
                                                </tbody>
                                            </Table>
                                        </div>
                                        :
                                        <div className="f-center" style={{ minHeight: 'calc(100vh - 225px)' }}>
                                            <EmptyWithIcon title={'Create a Gallery, to add feed! âŒ›'} desc={'Oops! Looks like you have deleted your gallery, create one by simply clicking on the button below.'} icon={'media'} button={{ action: () => checkPermission('contentGallery', 'createGallery', true)  ? this.props.TOGGLE_ADD_GALLERY(true) : {}, text: 'Create Gallery' }} isFullScreen={false} />
                                        </div>
                                    :
                                    <div style={{ minHeight: 'calc(100vh - 500px)' }}>
                                        <Loader />
                                    </div>
                                }
                            </div>
                        </Card.Body>
                        {/* <Card.Footer>
                            <div className='d-flex justify-content-between align-items-center'>
                                <div className="my-2 text-muted">
                                    Showing {startIdx + 1} to {startIdx + currentFeeds.length} of {manageFeeds?.feeds ? manageFeeds?.feeds.length : 0} entries
                                </div>
                                <div className="f-center">
                                    <span className='f-center text-muted fw-medium my-1'>Show
                                        <Form.Select className='mx-2' onChange={e => this.changeTotalPost(e)}>
                                            <option value="5" selected={showPost === 5}>5</option>
                                            <option value="10" selected={showPost === 10}>10</option>
                                            <option value="15" selected={showPost === 15}>15</option>
                                            <option value="20" selected={showPost === 20}>20</option>
                                            <option value="30" selected={showPost === 30}>30</option>
                                            <option value="40" selected={showPost === 40}>40</option>
                                        </Form.Select>
                                        entries
                                    </span>
                                    
                                    {totalPage > 1 && (
                                        <ul className="pagination pagination-sm my-1 ms-2">
                                            <li className="page-item">
                                                <span className={`page-link ${currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={() => { this.nextPrev(currentPage - 1) }}>
                                                    <FontAwesomeIcon icon={'angle-left'} />
                                                </span>
                                            </li>
                                            {pagination.map((page, i) => (
                                                page === '...' ? (
                                                    <li className="page-item" key={i}>
                                                        <span className="page-link">...</span>
                                                    </li>
                                                    ) : (
                                                    <li key={i} className={`page-item ${currentPage === page ? 'active' : ''}`} onClick={() => this.setState({ currentPage: page }, () => this.countAllPage())}>
                                                        <span className="page-link">{page}</span>
                                                    </li>
                                                    )
                                            ))}
                                            <li className="page-item">
                                                <span className={`page-link ${currentPage === totalPage ? ' disabled' : ' cursor-pointer'}`} onClick={() => { this.nextPrev(currentPage + 1) }}>
                                                    <FontAwesomeIcon icon={'angle-right'} />
                                                </span>
                                            </li>
                                        </ul>
                                    )}
                                </div>
                            </div>
                        </Card.Footer> */}
                    </Card>
                </Container>
            </div>
        </>
    }
}


const mapStateToProps = (state) => {
    return {

        manageFeeds: state.manageFeeds,
        wallsLoader: state?.dashboardData?.wallsLoader,
        allWalls: state?.dashboardData?.dashboarddata?.allWalls,

    }
}

const mapDispatchToProps = dispatch => {
    return {
        GET_FEEDS: () => dispatch(GET_FEEDS()),
        ACTION_DELETE_FEED: (feedId, networkId, callback) => dispatch(ACTION_DELETE_FEED(feedId, networkId, callback)),
        ACTION_FEED_STATUS: (feedId, status, action, callback) => dispatch(ACTION_FEED_STATUS(feedId, status, action, callback)),
        ACTION_UPDATE_SOCIAL_ACCOUNT: (accountId, feedId, callback) => dispatch(ACTION_UPDATE_SOCIAL_ACCOUNT(accountId, feedId, callback)),
        getModeration: () => dispatch(getModeration()),
        TOGGLE_ADD_GALLERY: (status) => dispatch({ type: TOGGLE_ADD_GALLERY, payload: status }),
        SNAPUP_DETAILS_ACTION: (callback) => dispatch(SNAPUP_DETAILS_ACTION(callback)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Managefeeds))